/**
 * Pricing Page
 * ✅ CODE-003: Migrated to TypeScript
 */

import { useState, type FC, type ReactNode } from 'react';
import { useNavigate } from 'react-router-dom';
import { Check, Crown, Zap, X } from 'lucide-react';
import { toast } from 'sonner';
import { apiPost } from '../utils/api';
import { logger } from '../utils/logger';

type PlanId = 'free' | 'basic' | 'professional' | 'enterprise';
type BillingPeriod = 'monthly' | 'annual';

interface PlanFeature { text: string; included: boolean; }
interface Plan {
  id: PlanId; name: string; nameHe: string; price: number; priceAnnual: number;
  currency: string; icon: ReactNode; color: string; features: PlanFeature[];
  popular?: boolean; perSeat?: boolean;
}

const PricingPage: FC = () => {
  const navigate = useNavigate();
  const [billingPeriod, setBillingPeriod] = useState<BillingPeriod>('monthly');
  const [loading, setLoading] = useState<Record<string, boolean>>({});

  const handleSelectPlan = async (tier: PlanId): Promise<void> => {
    if (tier === 'free') { toast.info('תכנית Free זמינה כברירת מחדל'); return; }
    setLoading({ ...loading, [tier]: true });
    try {
      const data = await apiPost('/api/payments/create-checkout-session', { tier, billing_period: billingPeriod }, { showErrorToast: false });
      window.location.href = data.url;
    } catch (error) {
      logger.error('Checkout error:', error);
      toast.error(error instanceof Error ? error.message : 'שגיאה ביצירת תשלום');
      setLoading({ ...loading, [tier]: false });
    }
  };

  const plans: Plan[] = [
    { id: 'free', name: 'Free', nameHe: 'ניסיון', price: 0, priceAnnual: 0, currency: '$', icon: <Zap className="w-8 h-8 text-gray-400" />, color: 'from-gray-600 to-gray-700', features: [{ text: '10 עמודים בלבד', included: true }, { text: 'תיק אחד', included: true }, { text: 'שבועיים ניסיון', included: true }, { text: 'ניתוח בסיסי', included: true }, { text: 'זיהוי סתירות', included: true }, { text: 'צ\'אט עם יתרו', included: true }, { text: 'Vision OCR', included: false }, { text: 'ייצוא', included: false }] },
    { id: 'basic', name: 'Basic', nameHe: 'בסיסי', price: 49, priceAnnual: 490, currency: '$', icon: <Check className="w-8 h-8 text-blue-400" />, color: 'from-blue-600 to-cyan-600', features: [{ text: '150 עמודים/חודש', included: true }, { text: '2 תיקים/חודש', included: true }, { text: 'AI חכם', included: true }, { text: 'ניתוח מתקדם', included: true }, { text: 'ייצוא Word', included: true }] },
    { id: 'professional', name: 'Professional', nameHe: 'מקצועי', price: 149, priceAnnual: 1490, currency: '$', icon: <Check className="w-8 h-8 text-purple-400" />, color: 'from-purple-600 to-pink-600', popular: true, features: [{ text: '1,000 עמודים/חודש', included: true }, { text: '5 תיקים/חודש', included: true }, { text: 'AI פרימיום', included: true }, { text: 'ניתוח מתקדם + Intelligence', included: true }, { text: 'ייצוא Word + Excel', included: true }] },
    { id: 'enterprise', name: 'Enterprise', nameHe: 'ארגוני', price: 349, priceAnnual: 3490, currency: '$', perSeat: true, icon: <Crown className="w-8 h-8 text-yellow-400" />, color: 'from-yellow-600 to-orange-600', features: [{ text: '2,500 עמודים/מושב/חודש', included: true }, { text: 'ניהול צוות', included: true }, { text: 'SSO/SAML', included: true }, { text: 'תמיכה 24/7', included: true }] }
  ];

  return (
    <div className="min-h-screen bg-gradient-to-br from-slate-900 via-purple-900 to-slate-900 py-12 px-4">
      <div className="max-w-7xl mx-auto">
        <div className="text-center mb-12">
          <h1 className="text-3xl sm:text-5xl font-bold text-white mb-4">בחר את התכנית המתאימה לך</h1>
          <div className="inline-flex bg-white/10 rounded-xl p-1 border border-white/20">
            <button onClick={() => setBillingPeriod('monthly')} className={`px-6 py-2 rounded-lg font-semibold ${billingPeriod === 'monthly' ? 'bg-white text-purple-900' : 'text-white'}`}>חודשי</button>
            <button onClick={() => setBillingPeriod('annual')} className={`px-6 py-2 rounded-lg font-semibold ${billingPeriod === 'annual' ? 'bg-white text-purple-900' : 'text-white'}`}>שנתי <span className="text-xs bg-green-500 px-2 py-0.5 rounded-full mr-2">חסכון 17%</span></button>
          </div>
        </div>
        <div className="grid grid-cols-1 md:grid-cols-4 gap-6">
          {plans.map((plan) => (
            <div key={plan.id} className={`relative bg-white/10 rounded-2xl p-6 border ${plan.popular ? 'border-purple-400 scale-105' : 'border-white/20'}`}>
              {plan.popular && <div className="absolute -top-4 left-1/2 -translate-x-1/2"><span className="bg-purple-600 text-white px-4 py-1 rounded-full text-sm font-bold">הכי פופולרי</span></div>}
              <div className="text-center mb-4">
                <div className={`inline-flex items-center justify-center w-14 h-14 rounded-full bg-gradient-to-r ${plan.color} mb-3`}>{plan.icon}</div>
                <h3 className="text-xl font-bold text-white">{plan.nameHe}</h3>
                <div className="text-3xl font-bold text-white">{plan.price === 0 ? 'חינם' : `$${billingPeriod === 'annual' ? Math.round(plan.priceAnnual / 12) : plan.price}/חודש`}</div>
              </div>
              <ul className="space-y-2 mb-6">{plan.features.map((f, i) => <li key={i} className="flex items-center gap-2">{f.included ? <Check className="w-4 h-4 text-green-400" /> : <X className="w-4 h-4 text-gray-500" />}<span className={f.included ? 'text-white text-sm' : 'text-gray-400 text-sm'}>{f.text}</span></li>)}</ul>
              <button onClick={() => handleSelectPlan(plan.id)} disabled={loading[plan.id] || plan.id === 'free'} className={`w-full py-2 rounded-xl font-bold ${plan.popular ? 'bg-purple-600 text-white' : plan.id === 'free' ? 'bg-white/20 text-white cursor-not-allowed' : 'bg-white/10 text-white hover:bg-white/20'}`}>{loading[plan.id] ? 'טוען...' : plan.id === 'free' ? 'תכנית נוכחית' : 'התחל עכשיו'}</button>
            </div>
          ))}
        </div>
        <div className="mt-8 text-center"><button onClick={() => navigate('/')} className="text-purple-200 hover:text-white">← חזור לדשבורד</button></div>
      </div>
    </div>
  );
};

export default PricingPage;
